:root {
	--custom-easing: cubic-bezier(.34, .1, .85, 1.04);
}

.wp-block-itmar-logo-anime {
	position: fixed !important;
	top: 0;
	left: 0;
	background: #504237;
	color: #ffffff;
	width: 100vw;
	height: 100vh;

	/* Loading背景画面設定　*/
	#splash {
		/*fixedで全面に固定*/
		position: fixed;
		width: 100vw;
		height: 100vh;
		z-index: 9999;
		text-align: center;
	}

	/* Loading画像中央配置　*/
	#splash_logo {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}

	/* Loading アイコンの大きさ設定　*/
	#splash_logo {
		svg {
			width: 250px;
		}
	}

	/*=============== SVGアニメーション内の指定 =================*/

	/*アニメーション前の指定*/
	#logo_anime {
		path {
			fill-opacity: 0;
			/*最初は透過0で見えない状態*/
			transition: fill-opacity .5s;
			/*カラーがつく際のアニメーション0.5秒で変化*/
			fill: none;
			/*塗りがない状態*/
			stroke: #fff;
		}

	}

	/*アニメーション後に.doneというクラス名がで付与された時の指定*/
	#logo_anime {
		&.done {
			path {
				fill: #fff;
				/*塗りの色*/
				fill-opacity: 1;
				/*透過1で見える状態*/
				stroke: none;

			}
		}

	}
}